@import url("https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Montserrat:ital,wght@0,400;0,600;0,700;1,400&display=swap");
@import "./App-mobile.css";


.App {
  touch-action: pan-y pinch-zoom;
  overscroll-behavior-x: touch;
}

.App > div {
  height: 85vh;
  overflow-y: visible; /* Change from auto to visible */
  scroll-behavior: smooth;
  scrollbar-width: none;
  -webkit-overflow-scrolling: touch;
  /* Remove scroll-snap-type from here */
}

/* Apply scroll-snap AND overflow to the same element */
.home-page {
  scroll-snap-type: y mandatory;
  height: 85vh;
  overflow-y: auto; /* Move the actual scrolling here */
  scroll-behavior: smooth;
}

/* Apply scroll-snap AND overflow to AboutMe page too */
.aboutme-page {
  scroll-snap-type: y mandatory;
  height: 85vh;
  overflow-y: auto;
  scroll-behavior: smooth;
}

/* Apply scroll-snap AND overflow to Projects page too */
.projects-page {
  scroll-snap-type: y mandatory;
  height: 85vh;
  overflow-y: auto;
  scroll-behavior: smooth;
}

.introduction,
.box-container,
.footer {
  scroll-snap-align: start;
  height: 85vh;
  width: 100%;
  scroll-snap-stop: always;
}

/* Keep other pages as single sections without scroll-snap */
.projects,
.aboutme {
  height: 85vh;
  width: 100%;
  overflow-y: auto; /* Add scrolling to other pages too */
}

.App * {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.aboutme p,
.projects p,
.contact-info,
input,
textarea,
.App > div {
  -webkit-user-select: text;
  -moz-user-select: text;
  -ms-user-select: text;
  user-select: text;
}

.App > div {
  outline: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.introduction,
.box-container,
.footer,
.projects,
.aboutme {
  scroll-snap-align: start;
  height: 85vh; /* Keep content at 85vh, now taller than 80vh container */
  width: 100%;
  scroll-snap-stop: always;
}

.introduction {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  border-bottom: none;
  border-top: none;
  max-width: 100%;
  color: var(--primary);
  font-family: "Montserrat", sans-serif;
  font-style: italic;
  font-weight: 400;
  font-size: 2.5rem;
  text-align: center;
  position: relative; 
}

.smiley {
  color: var(--primary);
  font-family: "Montserrat", sans-serif;
  font-style: italic;
  font-weight: 400;
  font-size: 3rem;
  text-align: center;
  transform: rotate(75deg);
  transition: transform 500ms;
}

.smiley:hover {
  transform: rotate(435deg);
}

.arrow-down {
  width: 4rem;
  height: 4rem;
  position: absolute;
  bottom: 4rem;
  left: 50%; 
  transform: translateX(-50%);
  color: var(--primary);
  transition: transform 500ms ease-in-out;
  animation: bounce 1.5s infinite;
}

@keyframes bounce {
  0%,
  20%,
  50%,
  80%,
  100% {
    transform: translateX(-50%) translateY(0);
  }
  40% {
    transform: translateX(-50%) translateY(-10px);
  }
  60% {
    transform: translateX(-50%) translateY(-5px);
  }
}

@keyframes simpleUpDown {
  0%,
  100% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-8px);
  }
}

@keyframes pulseBounce {
  0%,
  100% {
    transform: translateY(0) scale(1);
  }
  50% {
    transform: translateY(-8px) scale(1.1);
  }
}

.box-container {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  border-bottom: solid var(--secondary) 6px;
  min-width: 100%;
  max-width: 100%;
}

.box-container a {
  width: 100%;
  color: var(--secondary);
  text-align: center;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 3rem;
  text-decoration: none;
  transition: all 400ms;
}

.box-container a:hover {
  font-weight: bold;
  font-size: 4rem;
  transition: all 200ms;
}

.box-container a:active {
  text-decoration: line-through;
}

.box-container img {
  width: 60%;
  height: 60%;
  filter: grayscale(100%);
  border-radius: 50px;
  border: solid var(--primary) 4px;
  background-color: var(--secondary);
  transition: all 1000ms;
}

.box-container img:active {
  transform: scale(1.5);
}

.black-box1,
.white-box1,
.black-box2,
.white-box2 {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 100%;
  min-height: 100%;
}

.black-box1 {
  grid-column: 1;
  grid-row: 1;
  background: var(--primary);
}

.white-box1 {
  grid-column: 2;
  grid-row: 1;
  background: var(--secondary);
}

.black-box2 {
  grid-column: 2;
  grid-row: 2;
  background: var(--primary);
}

.white-box2 {
  grid-column: 1;
  grid-row: 2;
  background: var(--secondary);
}

.footer {
  display: flex;
  flex-direction: column;
  min-width: 100%;
  max-height: 10em;
  background: var(--primary);
  text-align: center;
  align-items: center;
  justify-content: center;
}

.socials {
  margin-top: 2rem;
}

.socials img {
  margin-left: 25px;
  margin-right: 25px;
  transition: transform 400ms;
}

.socials img:hover {
  transform: scale(1.3);
  transition: transform 200ms;
}

.contact-info {
  margin-top: 2rem;
  margin-bottom: 2rem;
}

.contact-info ul {
  display: flex;
  flex-direction: row;
  min-width: 100%;
  font-family: "Lato", sans-serif;
  font-weight: normal;
  list-style: none;
  color: var(--secondary);
  align-items: center;
  justify-content: center;
  padding: 0;
}

.contact-info li {
  margin: 0 50px;
  transition: all 400ms;
}

.contact-info li:hover {
  cursor: default;
  font-weight: bold;
  transform: scale(1.3);
  transition: all 200ms;
}

.contact-info a {
  color: inherit;
  text-decoration: none;
}

.aboutme {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  min-width: 100%;
  scroll-snap-align: start;
  height: 85vh;
  width: 100%;
  scroll-snap-stop: always;
}

.projects {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  max-width: 100%;
  min-width: 100%;
}

.projects-clickme {
  color: var(--secondary);
  margin: auto 1rem auto 0;
}

.aboutgrey {
  filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
  transition: all 500ms ease-out;
  max-width: 20vw;
  border: 6px var(--primary) solid;
}

.aboutgrey:hover {
  filter: none;
  -webkit-filter: grayscale(0);
  transition: all 500ms ease-in;
}

.aboutme h1 {
  color: var(--primary);
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 4rem;
  text-align: center;
  margin: 3rem;
  transition: text-decoration 400ms;
  max-width: 100%;
}
.projects h1 {
  color: var(--primary);
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 4rem;

  text-align: center;
  margin: 3rem;
  transition: text-decoration 400ms;
  max-width: 100%;
}

.aboutme p {
  color: var(--primary);
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 1.2rem;
  text-align: center;
  margin: 2rem 0 0 0;
  line-height: 2rem;
  padding: 5rem;
  min-width: 75vw;
  max-width: 50vw;
}

.projects p {
  color: var(--secondary);
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  font-size: 1.2rem;
  text-align: center;
  margin: 2rem;
  line-height: 2.5rem;
}

.dropdown {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 2.5rem auto;
  min-width: max-content;
}

.dropdown-content {
  position: absolute;
  background-color: var(--primary);
  color: var(--secondary);
  max-width: 0;
  font-size: 1rem;
  line-height: 2.5rem;
  max-height: 0;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.452);
  z-index: 1;
  text-align: center;
  border-radius: 25px;
  transition: all 600ms ease-in;
  overflow: hidden;
}

.dropdown-content p {
  opacity: 0;
  transition: all 1s ease-in;
}

.dropdown:hover .dropdown-content {
  display: block;
  max-height: 400px;
  max-width: 1000px;
}

.dropdown:hover .dropdown-content p {
  opacity: 1;
}

.greyimg {
  filter: grayscale(100%);
  -webkit-filter: grayscale(100%);
  transition: filter 1s ease-in;
  border-radius: 4em;
  max-width: 75vw;
  height: auto;
  border: 6px solid var(--primary);
  cursor: pointer;
}

.greyimg:hover,
.dropdown:hover .greyimg {
  filter: none;
  -webkit-filter: grayscale(0);
}

.dropdown::after {
  content: "▼";
  position: absolute;
  top: 2em;
  right: 3em;
  background: var(--primary);
  color: var(--secondary);
  padding: 8px 10px;
  border-radius: 50%;
  font-size: 1.6em;
  opacity: 0.7;
  transition: opacity 0.3s ease;
  pointer-events: none;
  z-index: 2;
  animation: gentleBounce 2s ease-in-out infinite;
}

.dropdown:hover::after {
  opacity: 0.2;
  animation: none; /* Stop animation on hover */
}

/* Hide hint when dropdown is active */
.dropdown.active::after {
  opacity: 0;
  animation: none;
}

@keyframes gentleBounce {
  0%,
  80%,
  100% {
    transform: translateY(0);
  }
  40% {
    transform: translateY(-4px);
  }
}

.github-link {
  color: var(--secondary);
  font-family: "Montserrat", sans-serif;
  font-weight: 600;
  font-size: 1.2rem;
  text-decoration: none;
  display: inline-block;
  margin-bottom: 2rem;
  animation: gentleBounce 2s infinite;
}

.github-img {
  width: 1.5rem;
  height: 1.5rem;
  margin-left: 0.5rem;
  vertical-align: middle;
}
